outline-none\n                          transition\n                          ease-in-out\n                          duration-150\n                        \"\n                      >\n                        {{ $page.props.user.name }}\n\n                        <svg\n                          class=\"ml-2 -mr-0.5 h-4 w-4\"\n                          xmlns=\"http://www.w3.org/2000/svg\"\n                          viewBox=\"0 0 20 20\"\n                          fill=\"currentColor\"\n                        >\n                          <path\n                            fill-rule=\"evenodd\"\n                            d=\"M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z\"\n                            clip-rule=\"evenodd\"\n                          />\n                        </svg>\n                      </button>\n                    </span>\n                  </template>\n\n                  <template #content>\n                    <!-- Account Management -->\n                    <div class=\"block px-4 py-2 text-xs text-gray-400\">\n                      Manage Account\n                    </div>\n\n                    <jet-dropdown-link :href=\"route('profile.show')\">\n                      Profile\n                    </jet-dropdown-link>\n\n                    <div class=\"border-t border-gray-100\"></div>\n\n                    <!-- Authentication -->\n                    <form @submit.prevent=\"logout\">\n                      <jet-dropdown-link as=\"button\">\n                        Log Out\n                      </jet-dropdown-link>\n                    </form>\n                  </template>\n                </jet-dropdown>\n              </div>\n            </div>\n\n            <!-- Hamburger -->\n            <div class=\"-mr-2 flex items-center sm:hidden\">\n              <button\n                @click=\"showingNavigationDropdown = !showingNavigationDropdown\"\n                class=\"\n                  inline-flex\n                  items-center\n                  justify-center\n                  p-2\n                  rounded-md\n                  text-gray-400\n                  hover:text-gray-500\n                  hover:bg-gray-100\n                  focus:outline-none\n                  focus:bg-gray-100\n                  focus:text-gray-500\n                  transition\n                  duration-150\n                  ease-in-out\n                \"\n              >\n                <svg\n                  class=\"h-6 w-6\"\n                  stroke=\"currentColor\"\n                  fill=\"none\"\n                  viewBox=\"0 0 24 24\"\n                >\n                  <path\n                    :class=\"{\n                      hidden: showingNavigationDropdown,\n                      'inline-flex': !showingNavigationDropdown,\n                    }\"\n                    stroke-linecap=\"round\"\n                    stroke-linejoin=\"round\"\n                    stroke-width=\"2\"\n                    d=\"M4 6h16M4 12h16M4 18h16\"\n                  />\n                  <path\n                    :class=\"{\n                      hidden: !showingNavigationDropdown,\n                      'inline-flex': showingNavigationDropdown,\n                    }\"\n                    stroke-linecap=\"round\"\n                    stroke-linejoin=\"round\"\n                    stroke-width=\"2\"\n                    d=\"M6 18L18 6M6 6l12 12\"\n                  />\n                </svg>\n              </button>\n            </div>\n          </div>\n        </div>\n\n        <!-- Responsive Navigation Menu -->\n        <div\n          :class=\"{\n            block: showingNavigationDropdown,\n            hidden: !showingNavigationDropdown,\n          }\"\n          class=\"sm:hidden\"\n        >\n          <div class=\"pt-2 pb-3 space-y-1\">\n            <jet-responsive-nav-link\n              :href=\"route('AdminControlPanel.index')\"\n              :active=\"route().current('AdminControlPanel.index')\"\n            >\n              Admin <br />\n              Control Panel\n            </jet-responsive-nav-link>\n\n            <jet-responsive-nav-link\n              :href=\"route('dashboard')\"\n              :active=\"route().current('dashboard')\"\n            >\n              Admin <br />\n              Dashboard\n            </jet-responsive-nav-link>\n\n            <jet-responsive-nav-link\n              :href=\"route('newsfeed.index')\"\n              :active=\"route().current('newsfeed.index')\"\n            >\n              Newsfeed\n            </jet-responsive-nav-link>\n\n            <jet-responsive-nav-link\n              :href=\"route('eCommerce.index')\"\n              :active=\"route().current('eCommerce.index')\"\n            >\n              eCommerce Demo\n            </jet-responsive-nav-link>\n          </div>\n\n          <!-- Responsive Settings Options -->\n          <div class=\"pt-4 pb-1 border-t border-gray-200\">\n            <div class=\"flex items-center px-4\">\n              <div\n                v-if=\"$page.props.jetstream.managesProfilePhotos\"\n                class=\"flex-shrink-0 mr-3\"\n              >\n                <img\n                  class=\"h-10 w-10 rounded-full object-cover\"\n                  :src=\"$page.props.user.profile_photo_url\"\n                  :alt=\"$page.props.user.name\"\n                />\n              </div>\n\n              <div>\n                <div class=\"font-medium text-base text-gray-800\">\n                  {{ $page.props.user.name }}\n                </div>\n              </div>\n            </div>\n\n            <div class=\"mt-3 space-y-1\">\n              <jet-responsive-nav-link\n                :href=\"route('profile.show')\"\n                :active=\"route().current('profile.show')\"\n              >\n                Profile\n              </jet-responsive-nav-link>\n\n              <!-- Authentication -->\n              <form method=\"POST\" @submit.prevent=\"logout\">\n                <jet-responsive-nav-link as=\"button\">\n                  Log Out\n                </jet-responsive-nav-link>\n              </form>\n            </div>\n          </div>\n        </div>\n      </nav>\n\n      <!-- Page Heading -->\n      <header class=\"bg-white shadow\" v-if=\"$slots.header\">\n        <div class=\"max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8\">\n          <slot name=\"header\"></slot>\n        </div>\n      </header>\n\n      <!-- Page Content -->\n      <main>\n        <slot></slot>\n      </main>\n\n      <!-- copyright trademark -->\n      <footer>\n        <div\n          class=\"\n            font-serif\n            text-xs text-gray-600\n            flex\n            justify-center\n            mt-12\n            pb-6\n          \"\n        >\n           {{ new Date().getFullYear() }} by Pad6y.com All rights reserved.\n        </div>\n      </footer>\n    </div>\n  </div>\n</template>\n\n<script>\nimport JetApplicationMark from \"@/Jetstream/ApplicationMark\";\nimport JetBanner from \"@/Jetstream/Banner\";\nimport JetDropdown from \"@/Jetstream/Dropdown\";\nimport JetDropdownLink from \"@/Jetstream/DropdownLink\";\nimport JetNavLink from \"@/Jetstream/NavLink\";\nimport JetResponsiveNavLink from \"@/Jetstream/ResponsiveNavLink\";\n\nexport default {\n  components: {\n    JetApplicationMark,\n    JetBanner,\n    JetDropdown,\n    JetDropdownLink,\n    JetNavLink,\n    JetResponsiveNavLink,\n  },\n\n  data() {\n    return {\n      showingNavigationDropdown: false,\n    };\n  },\n\n  methods: {\n    logout() {\n      this.$inertia.post(route(\"logout\"));\n    },\n  },\n};\n</script>\n\n<style>\nbody.swal2-toast-shown .swal2-container.swal2-top-end,\nbody.swal2-toast-shown .swal2-container.swal2-top-right {\n  top: 60px;\n  right: 60px;\n}\n</style>"],"sourceRoot":""}]);
